'use client'

import { motion } from 'framer-motion'
import Link from 'next/link'
import Image from 'next/image'
import OrcamentoModal from '../../components/OrcamentoModal'
import { useState } from 'react'

// --- DADOS DOS PRODUTOS ---
const produtos = [
  {
    id: 'camisetas',
    title: 'Camisetas',
    image: '/images/brindes/Camiseta Preta.png', // Imagem de fundo
    color: 'text-ls-cobre',
    details: [
      {
        subtitle: 'Serigrafia',
        text: 'Ideal para grandes quantidades e designs com cores sólidas. Durabilidade e um acabamento clássico para uniformes e eventos.',
      },
      {
        subtitle: 'Sublimação',
        text: 'Perfeita para estampas coloridas, fotos e designs complexos sem limite de cores. Toque zero e qualidade fotográfica.',
      },
    ],
  },
  {
    id: 'copos',
    title: 'Copos Plásticos',
    image: '/images/brindes/8.png', // Imagem de fundo
    color: 'text-ls-laranja',
    details: [
      {
        text: 'Personalize copos para festas, eventos e ações de marketing. Disponíveis em diversos modelos e cores, são uma forma divertida e útil de divulgar sua marca.',
      },
    ],
  },
  {
    id: 'canecas',
    title: 'Canecas Metálicas',
    image: '/images/brindes/9.jpg', // Imagem de fundo
    color: 'text-ls-cobre',
    details: [
      {
        text: 'Um brinde premium e durável. Nossas canecas metálicas podem ser personalizadas com sua logo, ideais para presentes corporativos e para clientes especiais.',
      },
    ],
  },
];

export default function BrindesPage() {
  const [isModalOpen, setIsModalOpen] = useState(false);

  return (
    <>
      <main className="flex flex-col text-ls-branco min-h-screen">
        {/* Hero Section */}
        <header className="relative h-screen flex flex-col justify-center items-center text-center px-4 overflow-hidden">
          {/* Gradiente no final da seção para transição suave */}
          <div className="absolute inset-x-0 bottom-0 h-1/3 bg-gradient-to-t from-ls-preto to-transparent z-10" />

          <motion.h1
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5 }}
            className="text-5xl md:text-7xl font-extrabold text-ls-branco"
          >
            Brindes que Marcam
          </motion.h1>
          <motion.p 
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
            className="text-lg text-gray-300 mt-4 max-w-2xl mx-auto"
          >
            Transforme sua marca em um presente inesquecível com nossos produtos personalizados de alta qualidade.
          </motion.p>
        </header>

        {/* Seções de Produtos */}
        {produtos.map((produto) => (
          <section 
            key={produto.id} 
            className={`relative h-[80vh] md:h-screen flex items-center overflow-hidden ${
              produto.id === 'copos' ? 'justify-end' : 'justify-start'
            }`}
          >
            {/* Imagem de Fundo com Overlay */}
            <div className="absolute inset-0 z-0">
              <Image 
                src={produto.image} 
                alt={`Fundo para ${produto.title}`} 
                fill 
                className="object-cover"
                quality={80}
              />
              {/* Overlay escuro + Gradiente para transição suave */}
              <div className="absolute inset-0 bg-gradient-to-t from-ls-preto to-transparent" />
              {/* Gradiente no topo para suavizar a entrada */}
              <div className="absolute inset-0 bg-gradient-to-b from-ls-preto to-transparent" />
            </div>
            
            {/* Card de Conteúdo */}
            <div className="relative z-10 container mx-auto px-4">
              <motion.div
                initial={{ opacity: 0, x: produto.id === 'copos' ? 50 : -50 }}
                whileInView={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.8, ease: 'easeOut' }}
                viewport={{ once: true, amount: 0.4 }}
                className="max-w-md bg-black/30 backdrop-blur-md border border-white/10 p-8 rounded-2xl"
              >
                <h2 className={`text-4xl md:text-5xl font-bold mb-6 ${produto.color}`}>{produto.title}</h2>
                {produto.details.map((detail, index) => (
                  <div key={index} className="mb-4 last:mb-0">
                    {detail.subtitle && <h3 className="text-xl font-semibold text-white mb-1">{detail.subtitle}</h3>}
                    <p className="text-lg text-gray-300">{detail.text}</p>
                  </div>
                ))}
              </motion.div>
            </div>
          </section>
        ))}

        {/* Seção Final de CTA */}
        <section className="relative py-32 text-center flex flex-col items-center overflow-hidden">
          {/* Gradiente no início da seção para transição suave */}
          <div className="absolute inset-x-0 top-0 h-1/3 bg-gradient-to-b from-ls-preto to-transparent z-10" />

          <motion.h2 
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true, amount: 0.5 }}
            transition={{ duration: 0.6 }}
            className="text-4xl font-bold mb-4"
          >
            Pronto para Criar?
          </motion.h2>
          <motion.p 
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true, amount: 0.5 }}
            transition={{ duration: 0.6, delay: 0.2 }}
            className="text-lg text-gray-300 mb-8 max-w-lg"
          >
            Nossa equipe está pronta para transformar sua ideia em um brinde memorável.
          </motion.p>
          <motion.div 
            initial={{ opacity: 0, scale: 0.9 }}
            whileInView={{ opacity: 1, scale: 1 }}
            viewport={{ once: true, amount: 0.5 }}
            transition={{ duration: 0.6, delay: 0.4 }}
            className="flex flex-col sm:flex-row items-center justify-center gap-6"
          >
            <button 
              onClick={() => setIsModalOpen(true)}
              className="
                bg-ls-laranja/20 backdrop-blur-md border border-ls-laranja/50 text-ls-branco 
                font-bold py-4 px-10 rounded-full text-xl
                hover:bg-ls-laranja/30 hover:border-ls-laranja transition-all"
            >
              Fale Conosco
            </button>
            <Link 
              href="/portifolio?filter=brindes"
              className="
                bg-white/10 backdrop-blur-md border border-white/20 text-ls-branco 
                font-bold py-4 px-10 rounded-full text-xl
                hover:bg-white/20 hover:border-white/30 transition-all"
            >
              Ver Portfólio
            </Link>
          </motion.div>
        </section>
      </main>

      <OrcamentoModal isOpen={isModalOpen} onClose={() => setIsModalOpen(false)} />
    </>
  );
}